/* wrap */
.wrap {
  min-width: 320px;
  max-width: 1440px;
  width: 100%;
  height: 100%;
  margin: 0 auto; }

/* main */
.main {
  max-width: 1120px;
  width: 100%; }

/* header */
.header__nav {
  display: flex;
