.header__left {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  padding-left: 0; }

.header__logo {
  display: flex;
  margin-right: auto; }

.header__logo::before {
  display: flex;
  content: "";
  display: inline-block;
  width: 2px;
  height: 32px;
  background-image: url(/img/general/divider.svg);
  background-repeat: no-repeat;
  background-position: center;
  padding-left: 20px;
  padding-top: 28px; }

.header__right {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none; }

.header__feedback {
  display: none;
  list-style: none; }

.header__item {
  display: flex;
  align-items: center;
  gap: 10px; }

.header__item span {
  display: none; }

/* container */
.container {
  max-width: 1120px;
  width: 100%;
  margin: 0; }

.content__title {
  display: flex;
  margin: 0;
  margin-top: 20px;
  font-size: 28px;
  position: relative;
  padding-left: 16px;
  margin-bottom: 24px; }

.content__title::before {
  content: "";
  display: inline-block;
  position: absolute;
  left: 0;
  top: 0;
  width: 4px;
  height: 40px;
  background-image: url(/img/general/highlight.svg);
  background-repeat: no-repeat; }

.content__list {
  display: flex;
  gap: 10px;
  margin-left: 10px;
  white-space: nowrap;
  overflow-y: hidden;
  width: 100%;
  list-style: none; }

.content__item {
  display: flex;
  text-decoration: none;
  font-size: 16px;
  padding: 5px 10px;
  color: #7E7E82;
  text-align: center;
  cursor: pointer;
  border: 2px solid;
  border-color: #F8F8F8; }

.content__item:hover {
  border-radius: 4px;
  border: 2px solid;
  border-color: #D9FFF5;
  background: #fff; }

.section__left {
  display: flex;
  flex-direction: column;
  gap: 20px; }

.content__text {
  display: flex;
  padding: 10px 10px 0;
  margin: 0; }

.content__text.desktop {
  display: none; }

.content__texts.desktop {
  display: none; }

.content__item-press {
  display: flex;
  text-decoration: none;
  display: inline-block;
  color: #1B1C21;
  font-weight: bold;
  margin-bottom: 20px; }

.content__item-press::before {
  content: "";
  display: inline-block;
  width: 7.41px;
  height: 10.29px;
  background-image: url(/img/general/expand.svg);
  background-repeat: no-repeat;
  background-position: center;
  margin-left: 30px; }

.content__reed {
  display: flex; }

.pictures {
  width: 100%;
  height: 100%; }

.content__nav-desktop__button {
  display: none; }

#sidebarDialog {
  width: 320px;
  min-height: 100%;
  position: fixed;
  top: 0;
  right: auto;
  left: auto;
  border: none;
  box-shadow: 12px 15px 26px -1px rgba(34, 60, 80, 0.2);
  z-index: 1000;
  background: #fff;
  transition: transform 0.3s ease; }

#sidebarDialog::-webkit-backdrop {
  background: #fffdfd; }

#sidebarDialog::backdrop {
  background: #fffdfd; }

#closeSidebar {
  display: block;
  cursor: pointer; }

.modal__overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fffdfd;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s; }

.modal__overlay--active {
  opacity: 1;
  visibility: visible; }

.modal__container {
  background: #fff;
  border-radius: 8px;
  width: 100%;
  max-width: 400px;
  padding: 20px;
  box-shadow: 0 2px 10px rgba(34, 60, 80, 0.2);
  transform: translateY(20px);
  transition: transform 0.3s ease; }

.modal__overlay--active .modal__container {
  transform: translateY(0); }

.modal__header {
  display: flex;
  flex-direction: column;
  align-self: flex-start; }

.modals__header {
  display: flex;
  flex-direction: column;
  align-self: flex-start; }

.modal__close,
.modal__title {
  margin-right: auto; }

.modals__close,
.modals__title {
  margin-right: auto; }

.modals__overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(34, 60, 80, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
