  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s; }

.modals__overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fffdfd;
  display: flex;
  justify-content: center;
  z-index: 2000; }

.modals__container {
  margin-left: auto;
  background: #fff;
  border-radius: 8px;
  width: 100%;
  height: 100%;
  max-width: 400px;
  padding: 20px;
  box-shadow: 0 2px 10px rgba(34, 60, 80, 0.2); }

.modals__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px; }

.modals__title {
  display: flex;
  margin-right: auto;
  font-size: 20px;
  color: #1B1C21; }

.modals__close {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: #B5B6BC; }

.forms__input {
  width: 100%;
  padding: 12px;
  border: 1px solid #EAEAEA;
  border-radius: 4px;
  font-size: 16px;
  margin-bottom: 15px; }

.forms__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap; }

.forms__notice {
  font-size: 12px;
  color: #7E7E82;
  margin-bottom: 15px;
  flex: 1;
  min-width: 200px; }

.forms__notice a {
  text-decoration: none;
  margin-left: auto;
  color: #FF3E79; }

.submits__button {
  margin-left: auto;
  color: #fff;
  border: none;
  padding: 12px 24px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s; }

.container__sidebar {
  max-width: 320px;
  width: 100%;
  min-height: 100%;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  z-index: 1000; }

/* header */
.header__sidebar {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 30px;
  list-style: none; }

.header__sidebar-item--right {
  margin-left: auto;
  margin-right: 0; }

/* sidebar */
.nav__sidebar {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-bottom: 50px; }

.nav__menu-link {
  color: #1B1C21;
  padding: 5px 20px;
  position: relative;
  display: flex;
  align-items: center;
  text-decoration: none; }

.nav__menu-link.active {
  color: #7E7E82; }

.nav__menu-link.active::before,
.nav__menu-link:hover::before {
  content: '';
  display: flex;
  position: absolute;
  left: 0;
  background: #41F6D7;
  border-radius: 0px 100px 100px 0;
  width: 5px;
  height: 100%; }

.nav__menu-link:hover {
  color: #7E7E82; }

/* footer */
.footer__sidebar {
  display: flex;
  padding-top: 60px;
  flex-direction: column;
  gap: 10px; }

.footer__sidebar-list {
  display: flex; }

.footer__contacts {
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-style: normal;
  margin-bottom: 30px; }

.contact__item {
  color: #1B1C21; }

.footer__contacts-tel {
  font-size: 24px;
  font-weight: bold; }

.footer__language {
  display: flex; }

.language__button.active {
  color: #7E7E82; }

.language__button:hover {
  color: #7E7E82; }

* {
  box-sizing: border-box; }

html {
  min-height: 100%; }

body {
  font-family: "TT Lakes";
  margin: 0;
  background-color: #F8F8F8; }

button {
  border: none;
  background: none;
  cursor: pointer;
  background-color: transparent;
  transition: .25s; }

a {
  border: none;
  background: none;
  cursor: pointer;
  background-color: transparent;
  transition: .25s; }

a:hover {
  transform: scale(1.1);
  opacity: 0.33; }

button:hover {
  transform: scale(1.1);
  opacity: 0.33; }

ul {
  list-style: none;
  padding: 0;
  margin: 0; }

li {
  list-style: none; }
